<template>
  <main class="contact">
    <div class="white-background-wrap">
      <div class="contact-intro" id="main" tabindex="0">
        <h2 class="section-header">contact</h2>
        <p>Lowco is always on the lookout for new opportunities or to hear from her community, feel free to contact her using the form below.</p>
      </div>
    </div>
    <div class="contact-form">
      <form id="contactForm">
        <transition name="div-fade" mode="out-in">
          <div v-if="!error && !loading && !response" key="base">
            <div class="row">
              <label class="form-label email">
                <input class="form-control" v-model="email" placeholder="Your email" :class="{error: invalidEmail}" type="email">
              </label>
              <label class="form-label subject">
                <input class="form-control" v-model="subject" placeholder="Subject" :class="{error: invalidSubject}" type="text">
              </label>
            </div>
            <div class="row">
              <label class="form-label">
                <textarea rows="5" class="form-control" v-model="message" placeholder="Your message" :class="{error: invalidMessage}" aria-label="Your message"></textarea>
              </label>
            </div>
            <div class="row">
              <button @click="sendMessage($event)" class="submit" title="Send">Send</button>
              <social-media-icons class="social"></social-media-icons>
            </div>
          </div>
          <div v-else-if="loading" key="loading">
            <loading-spinner></loading-spinner>
          </div>
          <div v-else-if="response" key="response" class="response">
            <label class="form-label target">{{target}}</label>
            <div>{{responseMessege}}</div>
          </div>
        </transition>
      </form>
    </div>
  </main>
</template>

<script>
  import FormData from 'form-data'
  import LoadingSpinner from './global/Loading'
  import SocialMediaIcons from './header/SocialMediaIcons'

  export default {
    name: 'contact',
    data () {
      return {
        email: '',
        invalidEmail: false,
        invalidMessage: false,
        invalidSubject: false,
        loading: false,
        error: false,
        response: false,
        message: '',
        subject: '',
        responseMessege: '',
        target: 'contact@lowco.tv'
      }
    },
    methods: {
      sendMessage (event) {
        event.preventDefault()
        if (!this.isValid(this.email)) {
          this.invalidEmail = true
        } else if (this.subject === '') {
          this.invalidSubject = true
        } else if (this.message === '') {
          this.invalidMessage = true
        } else {
          this.invalidEmail = false
          this.invalidMessage = false
          this.loading = true
          var formData = new FormData()
          formData.append('target', this.target)
          formData.append('email', this.email)
          formData.append('message', this.message)
          this.$http.post('php/contact.php', formData).then(function (response) {
            this.responseMessege = 'Message Sent!'
            this.message = ''
            this.loading = false
            this.response = true
          }, function () {
            this.loading = false
            this.response = true
            this.error = true
            this.responseMessege = 'There was a problem sending the message. You might want to just email us directly'
          })
        }
      },
      isValid (email) {
        var re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/ // eslint-disable-line
        return re.test(email)
      }
    },
    components: {
      LoadingSpinner,
      SocialMediaIcons
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
@import '../variables.scss';
form{
  margin: 0 auto;
  max-width: 635px;
  div {
    display: flex;
    flex-direction: column;
  }
}

.submit{
  border: none;
  border-radius: 25px;
  border: 2px solid $light-purple;
  background-color: $dark-purple;
  padding: $standard-spacing-half $standard-spacing3;
  color: #f3f6ff;
  transition: all 0.3s ease-in-out;
  text-decoration: none;
  cursor: pointer;
  transition: $standard-transition;
  &:hover, &:focus{
    background-color: $light-purple;
    outline: none;
  }
}

.contact-intro{
  padding: $home-section-padding;
  max-width: 600px;
  height: 162px;
  margin: 0 auto;
}

.contact-form{
  background-color: $dark-purple;
  padding: $home-section-padding;
  flex: 1 0 auto;
}

.form-label {
  display: inline-block;
  flex: 1;
}

.email{
  margin-right: $standard-spacing;
}

.subject{
  margin-left: $standard-spacing;
}

.row{
  flex-direction: row;
  justify-content: space-between;
  padding: 0 $standard-spacing2 $standard-spacing2 $standard-spacing2;
}

.social{
  flex-direction: row;
  margin-right: -$standard-spacing;
}

.form-control {
  font-family: $font-stack;
  letter-spacing: 1px;
  color: $font-gray;
  display: block;
  width: 100%;
  box-sizing: border-box;
  padding: $standard-spacing-half $standard-spacing;
  line-height: 1.5;
  border: none;
  background-color: $font-white;
  border-radius: 15px;
  &:focus{
    box-shadow: 0 0 0 3px $light-purple;
    outline: none;
  }
  &.error{
    box-shadow: 0 0 0 3px $live-red;
  }
}

.response{
  background-color: $font-white;
  padding: $standard-spacing3;
  border-radius: 25px;
  .target{
    font-size: 2em;
    margin-bottom: $standard-spacing;
  }
}

</style>



